/** === STARBURST === **/

.burst {
    background: #ffeb3b;
    width: 90px;
    height: 90px;
    position: absolute;
    top: -30px;
    left: -30px;
    text-align: center;
    z-index: -1;
    -webkit-animation: spin 8s linear infinite;
    animation: spin 8s linear infinite;
}

@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.burst:before, 
.burst:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 90px;
    width: 90px;
    background: #ffeb3b;
}

.burst:before {
    -webkit-transform: rotate(30deg);
       -moz-transform: rotate(30deg);
        -ms-transform: rotate(30deg);
         -o-transform: rotate(30deg);
}

.burst:after {
    -webkit-transform: rotate(60deg);
       -moz-transform: rotate(60deg);
        -ms-transform: rotate(60deg);
         -o-transform: rotate(60deg);
}

/** === iPHONE === **/

.iphone-body {
	position: relative;
	background: #fff;
	height: 600px;
	width: 300px;
	margin: 50px auto;
	border-radius: 40px;
	-webkit-box-shadow: 0 6px 0px 0 #9999cc;
	        box-shadow: 0 6px 0px 0 #9999cc;
}

.iphone-body:before {
	content: "";
	display: block;
	position: relative;
	top: 36px;
	background: #e7ebec;
	height: 6px;
	width: 60px;
	border-radius: 6px;
	margin: 0 auto;
}

.iphone-body:after {
	content: "";
	display: block;
	position: relative;
	top: 86px;
	background: none;
	height: 34px;
	width: 34px;
	border: 6px solid #e7ebec;
	border-radius: 50%;
	margin: 0 auto;
}

.camera-1 {
	position: absolute;
	background: #e7ebec;
	height: 8px;
	width: 8px;
	border-radius: 8px;
	margin: 12px 0 0 146px;
}

.camera-2 {
	position: absolute;
	background: #e7ebec;
	height: 10px;
	width: 10px;
	border-radius: 10px;
	margin: 28px 0 0 90px;
}

.iphone-screen {
	position: relative;
	top: 70px;
	background: #fff/*#f5f7f8*/;
	height: 440px;
	width: 272px;
	margin: 0 auto;
	border: 4px solid #e7ebec;
	border-radius: 4px;
}

.banner {
	position: relative;
	background: #ff6699;
	height: 50px;
	width: 100%;
	margin: 0;
}

.content {
	position: relative;
	background: #666699;
	height: 40px;
	width: 40px;
	border-radius: 4px;
	margin: 20px 0 0 10px;
}

.content:before {
	content: "";
	display: block;
	background: #666699;
	height: 10px;
	width: 50px;
	margin: 4px 0 0 50px;
}

.content:after {
	content: "";
	display: block;
	background: none;
	height: 10px;
	width: 200px;
	border-top: 10px solid #e7ebec;
	border-bottom: 10px solid #e7ebec;
	margin: 10px 0 0 50px;
}

.content-break {
	position: relative;
	background: #e7ebec;
	height: 4px;
	width: 252px;
	margin: 30px auto 20px auto;
}

.content-photo {
	position: relative;
	background: #ffcccc;
	height: 100px;
	width: 200px;
	margin: 20px 0 -10px 60px;
}


